.btn-group {
    min-width: 100px;
}

.link-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    text-decoration: underline;
    display: inline;
    margin: 0;
    padding: 0;
}

.link-button:hover,
.link-button:focus {
    text-decoration: none;
}

.description, .error-message {
    font-size: .95rem;
    font-weight: 400;
}

.disabled {
    cursor: not-allowed;
}